						<view :style='{"width":"100%","padding":"0 0 20rpx 20rpx","fontSize":"24rpx"}'>价格区间</view>
						<view :style='{"width":"100%","alignItems":"center","flexWrap":"wrap","justifyContent":"center","display":"flex"}'>
							<input :style='{"border":"0","borderRadius":"40rpx","textAlign":"center","background":"#eee","width":"40%","lineHeight":"60rpx","height":"60rpx"}' v-model="searchForm.pricestart" placeholder="请输入开始价格">
							<view :style='{"padding":"0 10rpx","fontSize":"20rpx"}'>-</view>
							<input :style='{"border":"0","borderRadius":"40rpx","textAlign":"center","background":"#eee","width":"40%","lineHeight":"60rpx","height":"60rpx"}' v-model="searchForm.priceend" placeholder="请输入结束价格">
